
#manager {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow-y: hidden;
}
#manager nav {
  width: 240px;
  height: 100%;
  background-color: #eef1f6;
  overflow-x: hidden;
  z-index: 4;
}
@media screen and (min-height: 503px) {
#manager nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
}
#manager nav h1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
  width: 100%;
  height: 80px;
  font-size: 20px;
  font-weight: normal;
  cursor: pointer;
}
#manager nav .sign-out-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 72px;
}
#manager nav .sign-out-container .el-button {
  width: 200px;
}
#manager section {
  padding: 20px;
  width: calc(100% - 240px);
  height: 100%;
  overflow-y: auto;
}
#manager section .manager-page {
  max-width: 1680px;
  margin: 0 auto;
}
#manager section .manager-page h2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 20px;
  height: 40px;
  font-size: 20px;
  font-weight: normal;
}
#manager section .manager-page h3 {
  font-size: 18px;
  font-weight: normal;
  margin: 16px 0;
}



